* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: "Baskerville Old Face", "Hoefler Text", Garamond,
    "Times New Roman", serif;
}

.head-section {
  height: 100vh;
  width: 100%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;

  background-color: rgb(239, 233, 233);
}
.hold-carousal {
  height: 400px;
  width: 80%;

  overflow: hidden;

  position: relative;

  background-color: chartreuse;
  display: flex;
  justify-content: center;
  align-items: center;
}

.arrow-silde {
  height: 30px;
  width: 100%;

  position: absolute;
  top: 45%;
  padding: 0 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.left-arrow,
.right-arrow {
  width: 30px;
  height: 30px;
  background-color: rgba(127, 24, 24, 0.105);

  cursor: pointer;
  border-radius: 50%;

  font-size: 18px;
  color: rgb(190, 189, 189);

  display: flex;
  justify-content: center;
  border: none;
  outline: none;
  align-items: center;
}

.left-arrow:hover,
.right-arrow:hover {
  background-color: rgba(14, 2, 2, 0.5);
  color: rgb(232, 234, 234);
}

.silder_active_div_image {
  display: flex;
  height: 100%;
  width: 100%;
  opacity: 1;
}

.auto-image-silde {
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
}

.deactive {
  display: none;
}
.sildes-count-plate {
  position: absolute;
  bottom: 50px;
  left: 45%;
  background-color: rgb(153, 154, 155);
  padding: 5px 10px;
}

.bullet-section {
  position: absolute;
  bottom: 30px;
  display: flex;
}
.bullet-section .bullet-active-silder {
  height: 5px;
  width: 20px;
  border: 1px solid #000;
}
.bullet-section .bullet-inactive-silder {
  background-color: rgb(95, 61, 61);
  height: 3px;
  width: 30px;
  z-index: 7;
  display: block;
  border-radius: 20px;
  transition: all 0.5s linear ease-out;
  cursor: pointer;
}
@media (max-width: 1024px) {
}
@media (max-width: 768px) {
  .left-arrow,
  .right-arrow {
    width: 20px;
    height: 20px;
    background-color: rgba(127, 24, 24, 0.105);

    cursor: pointer;
    border-radius: 50%;

    font-size: 18px;
    color: rgb(190, 189, 189);

    display: flex;
    justify-content: center;
    align-items: center;
  }
}

@media (max-width: 480px) {
  .left-arrow,
  .right-arrow {
    width: 13px;
    height: 13px;
    background-color: rgba(127, 24, 24, 0.105);

    cursor: pointer;
    border-radius: 50%;

    color: rgb(190, 189, 189);

    border: none;
    outline: none;

    display: flex;
    justify-content: center;
    align-items: center;
  }
  .sildes-count-plate {
    position: absolute;
    bottom: 50px;
    left: 40%;
    background-color: rgb(153, 154, 155);
    padding: 5px 10px;
  }
}
